<template>
  <el-dialog
    width="700px"
    class="contact-dialog"
    v-dialog-out
    :title="title"
    :visible.sync="dialogVisible"
    :before-close="hideDialog"
  >
    <el-form
      ref="form"
      label-position="top"
      :model="formData"
      :rules="formRules"
    >
      <el-form-item label="姓名" prop="name">
        <el-input v-model="formData.name" placeholder="碳基人"></el-input>
      </el-form-item>

      <el-form-item prop="email" label="邮箱地址">
        <el-input
          v-model="formData.email"
          placeholder="请输入邮箱地址"
        ></el-input>
      </el-form-item>

      <el-form-item prop="phone" label="电话">
        <el-input v-model="formData.phone" placeholder="请输入电话"></el-input>
      </el-form-item>

      <el-form-item prop="message" label="留言">
        <el-input
          v-model="formData.message"
          placeholder="请给我们留言"
          type="textarea"
          :rows="5"
        ></el-input>
      </el-form-item>
    </el-form>
    <el-button type="primary" @click="submitForm" style="margin-top: 20px">
      立即试用</el-button
    >
  </el-dialog>
</template>

<script>
export default {
  name: "contactUsDialog",
  components: {},
  mixins: [],
  props: {},
  computed: {},
  watch: {},
  filters: {},
  data() {
    return {
      title: "联系我们",
      dialogVisible: false,
      formData: {
        name: "",
        email: "",
        phone: ""
      },
      formRules: {
        name: [
          {
            required: true,
            message: "请输入姓名",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true,
            message: "请输入邮箱",
            trigger: "blur"
          }
        ],
        phone: [
          {
            required: true,
            message: "请输入电话",
            trigger: "blur"
          }
        ],
        message: [
          {
            required: true,
            message: "请给我们留言",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    showDialog() {
      this.dialogVisible = true;
    },

    hideDialog() {
      this.dialogVisible = false;
      this.$refs.form.resetFields();
      this.formData = {};
    },

    submitForm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          // console.log(this.formData);
          this.$message.success("感谢您的加入！");
          this.hideDialog();
        } else {
          return false;
        }
      });
    }
  },
  created() {},
  mounted() {},
  destroyed() {}
};
</script>

